/* Job Overview Card */
.jobOverview {
  background: #fff;
  display: flex;
  flex-direction: column;
  gap: 0.7em;
  margin-top: 1.5rem;
  padding: 1.5rem 1.2rem;
  border-radius: 18px;
  box-shadow: 0 4px 18px rgba(0,0,0,0.08), 0 1.5px 6px rgba(0,0,0,0.03);
  transition: box-shadow 0.2s, transform 0.2s;
  border: 1.5px solid #e3e8ee;
  cursor: pointer;
  min-height: 180px;
}
.jobOverview:hover {
  box-shadow: 0 8px 32px rgba(0,0,0,0.13), 0 2px 8px rgba(0,0,0,0.06);
  transform: translateY(-4px) scale(1.01);
  border-color: #007bff;
}

.jobOverview h2 {
  color: #007bff;
  font-size: 1.15rem;
  font-weight: 700;
  margin-bottom: 0.2em;
  margin-top: 0;
  letter-spacing: 0.5px;
}

.jobOverview h3 {
  color: #555;
  font-size: 1rem;
  font-weight: 600;
  margin: 0.7em 0 0.1em 0;
}

.jobOverview p {
  color: #222;
  font-size: 1.05rem;
  margin: 0 0 0.2em 0;
  font-weight: 400;
  word-break: break-word;
}

/* Job Details */
.jobDetails {
  max-width: 520px;
  margin: 3em auto 0 auto;
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 4px 18px rgba(0,0,0,0.08), 0 1.5px 6px rgba(0,0,0,0.03);
  padding: 2.2em 2em 2em 2em;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5em;
}

.jobDetails h2 {
  color: #007bff;
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.5em;
  margin-top: 0;
  text-align: center;
}

.jobDetails p {
  color: #222;
  font-size: 1.08rem;
  margin: 0.2em 0;
  font-weight: 400;
  text-align: center;
}

.jobDetails strong {
  color: #007bff;
  font-weight: 600;
}

.jobButtonContainer {
  display: flex;
  gap: 1.2em;
  justify-content: center;
  margin: 2em 0 1.5em 0;
  width: 100%;
}

/* Jobs Container Grid */
.jobsContainer {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 2em;
  margin: 1.5em 1vw 0.5em 1vw;
}

/* Job Button */
.jobButton {
  padding: 0.9em 2em;
  background: #007bff;
  color: #fff;
  border: none;
  border-radius: 7px;
  font-size: 1.08rem;
  font-weight: 600;
  margin: 0 1em 1.2em 1em;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(0,0,0,0.07);
  transition: background 0.2s, transform 0.2s;
}
.jobButton:hover {
  background: #0056b3;
  transform: translateY(-2px) scale(1.04);
}

/* Load More Section */
.loadMoreContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2em 0;
}

/* Load More Button */
.loadButton {
  padding: 0.9em 2.5em;
  background: linear-gradient(90deg, #007bff 60%, #00c6ff 100%);
  color: #fff;
  border: none;
  border-radius: 8px;
  font-size: 1.08rem;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(0,0,0,0.07);
  transition: background 0.2s, color 0.2s, transform 0.2s;
}
.loadButton:hover {
  background: linear-gradient(90deg, #0056b3 60%, #00aaff 100%);
  color: #fff;
  transform: translateY(-2px) scale(1.04);
}

/* Form Group */
.formGroup {
  max-width: 420px;
  margin: 24px auto;
  padding: 18px 18px 10px 18px;
  background: #fafdff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.06);
  font-family: 'Segoe UI', Arial, sans-serif;
  font-size: 1rem;
  color: #333;
  display: flex;
  flex-direction: column;
  gap: 1em;
}

.formGroup input[type="text"],
.formGroup input[type="email"],
.formGroup input[type="number"],
.formGroup textarea {
  width: 100%;
  padding: 10px;
  border: 1.5px solid #c7d0e1;
  border-radius: 6px;
  font-size: 1rem;
  background: #fafdff;
  transition: border 0.2s, box-shadow 0.2s;
  margin-top: 6px;
  box-sizing: border-box;
}

.formGroup input[type="text"]:focus,
.formGroup input[type="email"]:focus,
.formGroup input[type="number"]:focus,
.formGroup textarea:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0 0 0 2px #b3d7ff;
}

/* Responsive Design */
@media (max-width: 900px) {
  .jobsContainer {
    grid-template-columns: 1fr;
    gap: 1.2em;
  }
  .jobOverview {
    padding: 1.2rem 0.7rem;
  }
}

@media (max-width: 600px) {
  .jobsContainer {
    grid-template-columns: 1fr;
    margin: 0.5em;
    gap: 1em;
  }
  .jobOverview {
    padding: 1rem 0.5rem;
    border-radius: 1em;
  }
  .formGroup {
    padding: 10px 4px 6px 4px;
    max-width: 98vw;
  }
  .jobButton {
    width: 100%;
    margin: 0 0 1em 0;
  }
  .jobButtonContainer {
    flex-direction: column;
    gap: 0.7em;
    align-items: stretch;
  }
}

.appliedUserRow {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1.5em;
  padding: 0.7em 1.2em;
  margin-bottom: 0.7em;
  border: 1.5px solid #e3e8ee;
  border-radius: 7px;
  background: #fafdff;
  font-size: 1.04rem;
  transition: border-color 0.2s, background 0.2s;
}

.appliedUserRow:hover {
  border-color: #4be37a;
  background: #eafff2;
}