.createForm {
  margin-top: 28px;
  padding: 22px 24px;
  background: linear-gradient(90deg, #e3f0ff 60%, #f7fafc 100%);
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.06);
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
}

.createForm h2 {
  margin-bottom: 18px;
  color: #007bff;
  font-size: 1.3rem;
  font-weight: 600;
  text-align: center;
}

.createForm form {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.createForm input[type="text"] {
  padding: 9px 12px;
  border: 1px solid #c7d0e1;
  border-radius: 6px;
  font-size: 1rem;
  background: #fafdff;
  transition: border 0.2s;
}

.createForm input[type="text"]:focus {
  border: 1.5px solid #007bff;
  outline: none;
  background: #fff;
}

.createForm button[type="submit"] {
  background: #007bff;
  color: #fff;
  border: none;
  padding: 10px 0;
  border-radius: 6px;
  font-size: 1.08rem;
  font-weight: 600;
  cursor: pointer;
  margin-top: 8px;
  transition: background 0.2s;
}

.createForm button[type="submit"]:hover {
  background: #0056b3;
}

.profileContainer {
  max-width: 900px;
  width: 60vw;
  min-width: 320px;
  margin: 40px auto;
  padding: 32px 24px;
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 6px 32px rgba(0,0,0,0.08), 0 1.5px 6px rgba(0,0,0,0.03);
  font-family: 'Segoe UI', Arial, sans-serif;
  transition: box-shadow 0.2s, width 0.2s;
}

.profileContainer:hover {
  box-shadow: 0 12px 40px rgba(0,0,0,0.13), 0 2px 8px rgba(0,0,0,0.06);
}

.profileTitle {
  text-align: center;
  color: #222;
  font-size: 2.2rem;
  font-weight: 700;
  margin-bottom: 28px;
  letter-spacing: 1px;
}

.profileSection {
  margin-top: 24px;
  padding: 18px 20px;
  background: linear-gradient(90deg, #f7fafc 60%, #e3f0ff 100%);
  border-radius: 10px;
  box-shadow: 0 1px 4px rgba(0,0,0,0.03);
  transition: background 0.2s;
}

.profileSection h2 {
  margin: 0 0 6px 0;
  color: #007bff;
  font-size: 1.1rem;
  font-weight: 600;
  letter-spacing: 0.5px;
}

.profileSection p {
  margin: 0 0 10px 0;
  color: #333;
  font-size: 1.08rem;
  font-weight: 400;
  word-break: break-all;
}

.loading {
  text-align: center;
  margin-top: 60px;
  font-size: 1.2rem;
  color: #888;
  letter-spacing: 1px;
}

@media (max-width: 600px) {
  .profileContainer {
    width: 100vw;
    max-width: 100vw;
    min-width: 0;
    padding: 10px 2vw;
    border-radius: 0;
    box-shadow: none;
  }
  .modalContent {
    max-width: 98vw;
    min-width: 0;
    padding: 16px 2vw;
    border-radius: 8px;
  }
  .profileSection {
    padding: 10px 4px;
    font-size: 1rem;
  }
  .profileTitle {
    font-size: 1.2rem;
  }
  .passwordForm input,
  .passwordInput {
    font-size: 1rem;
    padding: 10px 8px;
    width: 100%;
    box-sizing: border-box;
  }
  .passwordForm button,
  .editButton,
  .deleteButton {
    width: 100%;
    margin: 8px 0 0 0 !important;
    font-size: 1rem;
    padding: 12px 0;
  }
}

.passwordInput {
  padding: 10px 14px;
  border: 1.5px solid #c7d0e1;
  border-radius: 6px;
  font-size: 1rem;
  background: #fafdff;
  transition: border 0.2s, background 0.2s;
  margin-bottom: 8px;
}

.passwordInput:focus {
  border: 1.5px solid #007bff;
  background: #fff;
  outline: none;
}
/* Change Password Section */
.passwordForm {
  display: flex;
  flex-direction: column;
  gap: 14px;
  margin-top: 12px;
  background: linear-gradient(90deg, #fafdff 60%, #e3f0ff 100%);
  padding: 22px 20px;
  border-radius: 10px;
  box-shadow: 0 1px 6px rgba(0,0,0,0.06);
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
}

.passwordForm input[type="password"] {
  padding: 10px 14px;
  border: 1.5px solid #c7d0e1;
  border-radius: 6px;
  font-size: 1rem;
  background: #fafdff;
  transition: border 0.2s, background 0.2s;
}

.passwordForm input[type="password"]:focus {
  border: 1.5px solid #007bff;
  background: #fff;
  outline: none;
}

.passwordForm button[type="submit"] {
  background: linear-gradient(90deg, #007bff 60%, #0056b3 100%);
  color: #fff;
  border: none;
  padding: 10px 0;
  border-radius: 6px;
  font-size: 1.08rem;
  font-weight: 600;
  cursor: pointer;
  margin-top: 8px;
  transition: background 0.2s, box-shadow 0.2s;
  box-shadow: 0 2px 8px rgba(0,123,255,0.08);
}

.passwordForm button[type="submit"]:hover {
  background: linear-gradient(90deg, #0056b3 60%, #007bff 100%);
}

.passwordForm button[type="button"] {
  background: #f2806f;
  color: #fff;
  border: none;
  padding: 10px 0;
  border-radius: 6px;
  font-size: 1.08rem;
  font-weight: 600;
  cursor: pointer;
  margin-top: 8px;
  margin-left: 0;
  transition: background 0.2s;
}

.passwordForm button[type="button"]:hover {
  background: #d9534f;
}

/* Edit & Delete Buttons */
.editButton {
  background: linear-gradient(90deg, #28a745 60%, #218838 100%);
  color: #fff;
  border: none;
  padding: 9px 22px;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  margin-right: 8px;
  transition: background 0.2s, box-shadow 0.2s;
  box-shadow: 0 2px 8px rgba(40,167,69,0.08);
}

.editButton:hover {
  background: linear-gradient(90deg, #218838 60%, #28a745 100%);
}

.deleteButton {
  background: linear-gradient(90deg, #f44336 60%, #d32f2f 100%);
  color: #fff;
  border: none;
  padding: 9px 22px;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s, box-shadow 0.2s;
  box-shadow: 0 2px 8px rgba(244,67,54,0.08);
}

.deleteButton:hover {
  background: linear-gradient(90deg, #d32f2f 60%, #f44336 100%);
}
