.aboutPage {
  padding: 20px;
  max-width: 900px;
  margin: 0 auto;
  font-family: 'Segoe UI', Arial, sans-serif;
}

.personSection {
  margin-bottom: 40px;
}

.founderSection {
  margin-bottom: 32px;
  background: #fafdff;
  border-radius: 10px;
  padding: 18px 20px;
  box-shadow: 0 2px 8px rgba(0,123,255,0.06);
}

.techSection {
  background: #fafdff;
  border-radius: 10px;
  padding: 18px 20px;
  box-shadow: 0 2px 8px rgba(0,123,255,0.06);
}

.person {
  margin-bottom: 16px;
  min-width: 120px;
}

.sideBySide {
  display: flex;
  gap: 24px;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.footer {
  background-color: #f1f1f1;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  border-radius: 10px;
  margin-top: 40px;
}

.contact, .companyDetails {
  width: 45%;
}

@media (max-width: 700px) {
  .sideBySide {
    flex-direction: column;
    gap: 12px;
  }
  .footer {
    flex-direction: column;
    gap: 18px;
  }
  .contact, .companyDetails {
    width: 100%;
  }
}