.container {
  max-width: 800px;
  margin: 40px auto;
  padding: 32px 24px;
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 6px 32px rgba(0,0,0,0.08), 0 1.5px 6px rgba(0,0,0,0.03);
  font-family: 'Segoe UI', Arial, sans-serif;
  transition: box-shadow 0.2s;
}

.container:hover {
  box-shadow: 0 12px 40px rgba(0,0,0,0.13), 0 2px 8px rgba(0,0,0,0.06);
}

h1 {
  text-align: center;
  color: #222;
  font-size: 2.2rem;
  font-weight: 700;
  margin-bottom: 18px;
  letter-spacing: 1px;
}

h2 {
  color: #007bff;
  font-size: 1.3rem;
  font-weight: 600;
  margin-top: 32px;
  margin-bottom: 12px;
  letter-spacing: 0.5px;
}

h3 {
  color: #007bff;
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 12px;
  text-align: center;
}

p {
  color: #444;
  font-size: 1.08rem;
  margin-bottom: 18px;
}

.list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.item {
  background: linear-gradient(90deg, #f7fafc 60%, #e3f0ff 100%);
  border-radius: 10px;
  box-shadow: 0 1px 4px rgba(0,0,0,0.03);
  margin-bottom: 18px;
  padding: 18px 20px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  transition: background 0.2s;
}

.item strong {
  color: #222;
  font-size: 1.08rem;
}

.buttons {
  display: flex;
  gap: 10px;
  margin-top: 8px;
}

.buttons button {
  background: #007bff;
  color: #fff;
  border: none;
  padding: 7px 18px;
  border-radius: 5px;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s;
  font-size: 1rem;
}

.buttons button:hover {
  background: #0056b3;
}

.buttons button:last-child {
  background: #dc3545;
}

.buttons button:last-child:hover {
  background: #a71d2a;
}

.form {
  background: #fafdff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.06);
  padding: 28px 24px;
  max-width: 400px;
  margin: 40px auto 0 auto;
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.field {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.field label {
  color: #007bff;
  font-weight: 500;
  margin-bottom: 2px;
}

.field input,
.field textarea {
  padding: 9px 12px;
  border: 1px solid #c7d0e1;
  border-radius: 6px;
  font-size: 1rem;
  background: #fafdff;
  transition: border 0.2s;
  resize: vertical;
}

.field input:focus,
.field textarea:focus {
  border: 1.5px solid #007bff;
  outline: none;
  background: #fff;
}

.actions {
  display: flex;
  gap: 12px;
  justify-content: flex-end;
}

.actions button {
  background: #007bff;
  color: #fff;
  border: none;
  padding: 8px 20px;
  border-radius: 6px;
  font-size: 1.08rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
}

.actions button:hover {
  background: #0056b3;
}

.actions button[type="button"] {
  background: #f1f1f1;
  color: #333;
}

.actions button[type="button"]:hover {
  background: #e2e2e2;
}

@media (max-width: 600px) {
  .container {
    padding: 12px 2vw;
    max-width: 99vw;
  }
  .form {
    padding: 14px 6px;
    max-width: 98vw;
  }
  .item {
    padding: 10px 6px;
  }
}

.newsJobSection {
  display: flex;
  gap: 18px;
  /* justify-content: center; */
  margin: 28px 0 32px 0;
}

.newsJobButton {
  background: #007bff;
  color: #fff;
  border: none;
  padding: 12px 32px;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(0,0,0,0.07);
  transition: background 0.2s, transform 0.2s;
}

.newsJobButton:hover {
  background: #0056b3;
  transform: translateY(-2px) scale(1.04);
}