.news-wrapper {
    display: flex;
    flex-direction: column;
    height: 100%;        /* fill the grid cell */
  }
  
  .news-header {
    /* if you want it to stick at the top: */
    position: sticky;
    top: 0;
    background: inherit;
    z-index: 1;
    margin-bottom: 10px;
  }
  
  .news-list {
  flex: 1 1 auto;
  overflow-y: hidden;
  padding-right: 5px;
  transition: box-shadow 0.2s;
}

.news-list:hover {
  overflow-y: auto;
}  
  
  /* style each item */
  .news-item {
    margin-bottom: 15px;
  }
